body {
    background-color: #e5e5ff;
}

.game-wrapper {
    width: 800px;
    margin: 50px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }

  .game-wrapper h1, .game-wrapper h2 {
      text-align: center;
  }  



  .image-controls {
      width: 100px;
      margin: 20px;
  }

  .controls {
      margin-left: 150px;
  }

  .perfil-img {
      width: 120px;
      border-radius: 50%; 
     position: absolute;
  }

  .hud-wrapper {
      border: 1px solid #000;
      padding: 20px;
  }
  .hud {
      display: flex;
  }

  .hud-img {
    width: 120px;
    border-radius: 50%;
  }

  .hud-info {
      margin-left: 50px;
    flex-grow: 3;

  }

  .hp-bar {
      text-align: center;
      border: 1px solid #000;
      background-color: lightgreen
  } 
  .green-bar {
    text-align: center;
    border: 1px solid #000;
    background-color: lightgreen
} 
.yellow-bar {
    text-align: center;
    border: 1px solid #000;
    background-color: lightyellow
} 
.red-bar {
    text-align: center;
    border: 1px solid #000;
    background-color: red;
} 
